import React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton} from 'react-mdl';
import Car from "./carpr.png";
import Indpr from "./indpr.png";
import Colorpr from "./colorpr.png";


class Projects extends Component{
    constructor(props){
        super(props);
        this.state = {activeTab: 0};

    }

    toggleCategories(){

        if(this.state.activeTab === 0){
            return(
                <div className="projects-grid">
                      <Card shadow={5} style={{minWidth: '450', margin:'auto'}}>
                          <CardTitle style={{color:'#fff', height: '210px', backgroundImage:`url(${Indpr})`}}></CardTitle>
                          <CardText>This is a Information Website About Nizamabad City, Which is Located in Telangana India.Main Aim of Application is to give Information About Nizamabad Like History, Tourist Places, How To Reach.</CardText>
                           <CardActions border className="button-bootstrap">
                           <a href="https://gstsolutions.000webhostapp.com" rel="noopener noreferrer" target="_blank"><Button colored>Demo</Button></a>
                           <a href="https://github.com/gst2322/induru" rel="noopener noreferrer" target="_blank"><Button colored>Github</Button></a>
                           </CardActions>
                           <CardMenu style={{color: '#fff'}}>
                              <IconButton name="share"/>
                           </CardMenu>
                      </Card>

                 </div>


            )
        } else if(this.state.activeTab === 1) {
            return (
                <Card shadow={5} style={{minWidth: '450', margin:'auto'}}>
                <CardTitle style={{color:'#fff', height: '170px', backgroundImage:`url(${Car})`}}></CardTitle>
                <CardText>This is Animated website designed with HTML,Css only. in this Webpage Car Moves on Roads of Beautiful City.</CardText>
                 <CardActions border className="button-bootstrap">
                 <a href="https://gst2322.github.io/carbackground/" rel="noopener noreferrer" target="_blank"><Button colored>Live Demo</Button></a>
                 <a href="https://github.com/gst2322/carbackground" rel="noopener noreferrer" target="_blank"><Button colored>GitHub</Button></a>
                 </CardActions>
                 <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share"/>
                 </CardMenu>
            </Card>
            )
        }else if(this.state.activeTab === 2) {
            return (
                <Card shadow={5} style={{minWidth: '450', margin:'auto'}}>
                <CardTitle style={{color:'#fff', height: '170px', backgroundImage:`url(${Colorpr})`}}></CardTitle>
                <CardText>This color generator uses HTML, CSS, JavaScript, and jQuery to generate Color.
                There is a Search option,where we can search whatever color we want.</CardText>
                 <CardActions border className="button-bootstrap">
                 <a href="https://github.com/gst2322/colorchanger" rel="noopener noreferrer" target="_blank"><Button colored>Github</Button></a>
                 </CardActions>
                 <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share"/>
                 </CardMenu>
            </Card>
            )
        }

    }




    render() {
        return(
            <div>
              <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                <Tab>Induru Website</Tab>
                <Tab>Car Animation</Tab>
                <Tab>Color Picker</Tab>
              </Tabs>


                <Grid className="projects-grid">
                    <Cell col={12}>
                        <div className="content">{this.toggleCategories()}</div>
                    </Cell>
                </Grid>



            </div>
        )


    }
}

export default Projects;
